<template>
  <div class="app">
    <nav class="navbar">
      <ul>
        <li
          v-for="(tab, i) in tabs"
          :key="i"
          @click="goTo(tab)"
          class="button outline"
          :class="{ active: tab.isSelected }"
        >
          {{ tab.name }}
        </li>
      </ul>
    </nav>
    <router-view />
  </div>
</template>

<script>
export default {
  data: () => ({
    name: "App",
    tabs: [
      { name: "Home", url: "/", isSelected: true },
      { name: "Resumé", url: "/resume", isSelected: false },
      { name: "Projects", url: "/projects", isSelected: false },
      { name: "Contact", url: "/contact", isSelected: false },
    ],
  }),
  methods: {
    goTo(tab) {
      if (!tab.isSelected) {
        this.tabs.find((el) => el.isSelected).isSelected = false;
        tab.isSelected = true;
        this.$router.push(tab.url);
      }
    },
  },
};
</script>

